"use client";

import { ReactNode } from "react";

interface ModernCardProps {
  children: ReactNode;
  variant?: "default" | "glass" | "gradient";
  hover?: boolean;
  className?: string;
  onClick?: () => void;
}

export function ModernCard({ 
  children, 
  variant = "default", 
  hover = true,
  className = "",
  onClick 
}: ModernCardProps) {
  const baseStyles = "rounded-2xl p-6 transition-all duration-200";
  
  const variantStyles = {
    default: "bg-white shadow-md hover:shadow-lg",
    glass: "glass backdrop-blur-xl bg-white/70 border border-white/20 shadow-xl",
    gradient: "bg-gradient-to-br from-blue-500 to-purple-600 text-white shadow-xl",
  };
  
  const hoverStyles = hover ? "hover:-translate-y-1 cursor-pointer" : "";
  
  return (
    <div 
      className={`${baseStyles} ${variantStyles[variant]} ${hoverStyles} ${className}`}
      onClick={onClick}
    >
      {children}
    </div>
  );
}
